







































import { Component, Vue  } from 'vue-property-decorator';
import Global from '../modules/Global';

@Component({})
export default class DetailPage extends Vue {
    /** 返回 */
    private goBack() {
        this['$router'].go(-1);
    }

    /** 页面数据 */
    private get pageData() {
        let list = Global.homeData.columnList;
        let id: number | string = this['$route'].params.id;
        let item = list.filter(item => item.id == id)[0];
        return item;
    } 

    /**
     * @param path 跳转路径
     */
    private openDetail(path: string) {
        this['$router'].push(path);
        window.scrollTo(0, 0);
    }

    /**
     * @param path 跳转路径
     */
    private openLink(path: string) {
        window.open(path); 
    }

    mounted() {
        // console.log(this.pageData);
        window.scrollTo(0, 0);
    }
}

